import React, {useState} from 'react';
import Header from './Header';
import Home from './Home';
import Navbar from './Navbar';
import Activity from './Activity';
import Explore from './Explore';
import Profile from './Profile';
import NewPost from './NewPost';
import initialStore from '../utils/initialStore'
import css from './App.module.css';

function App(){
	const [page, setPage] = useState('home');
	const [store, setStore] = useState(initialStore);
		

	return(
		<div className={css.container}>
			<Header/>
			<main className={css.content}>
			{renderMain(page)}
		</main>
		<Navbar onNavChange={setPage}/>
		</div>
	);

	function addLike(postId){
		const like = {
			userId: store.currentUserId, 
			postId,
			datetime: new Date().toISOString()
		};
		
		setStore({
		  ...store,
		  likes: store.likes.concat(like)
		});
	  }

	function  removeLike(postId){
		this.setStore({
			...store,// spread props. make sure you understand this
			likes: store.likes.filter(like=>!(like.userId===store.currentUserId && like.postId===postId))
			});
	  }
	
	function renderMain(page){
		switch(page){
		case "home": return <Home store={store} onLike={addLike} onUnlike={removeLike}/>;
		case "activity": return <Activity/>;
		case "explore": return <Explore/>;
		case "profile": return <Profile store={store}/>;
		case "newpost": return <NewPost/>;
		default: return <Home/>;
		}
	}
}

export default App;
