import React from 'react';
import PostThumbnail from './PostThumbnail';
import publicURL from 'utils/publicURL';
import css from './Profile.module.css'

function Profile(props) {
  const {store} = props;
  const thumbnails = store.posts.filter(post=> post.userId === store.user.id).map(thumbnail=><PostThumbnail photo={thumbnail}/>);
  const profile = store.users.filter(user=> user.id === store.currentUserId);
  return(
    <div>
      <div className={css.row}>
        <img src={publicURL(profile.photo)} className={css.pic}/>
        {profile.id}
      </div>
      <div className={css.name}>
        {profile.name}
      </div>
      <div className={css.bio}>
        {profile.bio}
      </div>
      <div className={css.dash}>
        <div className={css.digits}>
          <span>{store.posts.filter(post=> post.userId === store.currentUserId).length}</span> 
          <span>{store.followers.filter(follower => follower.userId === store.currentUserId).length}</span> 
          <span>{store.following.filter(following => following.followerId === store.currentUserId).length}</span>
        </div>
        <div className={css.labels}>
          <span>posts</span> <span>followers</span> <span>following</span>
        </div>
      </div>
      <div className={css.posts}>
          {thumbnails}
      </div>
    </div>
  );
}
  
  export default Profile;