import React from 'react';
import css from './Post.module.css';
import timespan from '../utils/timespan';
import publicUrl from '../utils/publicURL';


function Post(props){
    const comments = props.comments
    const commentItems = comments.map((comment)=>
        <div className={css.postItem}> <span className={css.userId}>{comment.userId}  </span>{comment.text}</div>
    );

    function handleLike(){ 
        props.onLike(props.post.id);
    }
    function handleUnlike(){
        props.onUnlike(props.post.id);
    }
    
    return(
        <div className={css.post}>
            <div className={css.postItem}>
                <img src = {publicUrl(props.user.photo)} alt='userPhoto' className={css.userPhoto}></img>
                {props.user.id}
            </div>
            <div className={css.postItem}>
                <img src = {publicUrl(props.post.photo)} alt='post' className={css.postPhoto}></img>
            </div>
            <div className={css.postItem}>
                <button>
                    {props.likes.self?
                        <img src={publicUrl('/assets/unlike.svg')} alt='Unlike Action' onClick={handleUnlike}/>  :
                        <img src={publicUrl('/assets/like.svg')} alt='Like Action' onClick={handleLike}/> 
                    }
                </button>
                <button>
                    <img src={publicUrl('/assets/comment.svg')} alt='Comment Action'/>
                </button>
            </div>
            <div className={css.postItem}>
                {props.likes.count} likes
            </div>
            <div className={css.postItem}>
                <span className={css.userId}>{props.post.userId} </span> {props.post.desc}
            </div>
            {commentItems}
            <div className = {css.postItem}>
                {timespan(props.post.datetime)} ago
            </div>
        </div>
    )
};

export default Post;